import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import './Login.css';
import { auth } from "./firebase";

function Login() {
  const navigate = useNavigate();
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const signIn = e => {
    e.preventDefault();
    //Firebase login
    auth
      .signInWithEmailAndPassword(email, password)
      .then(auth => {
        navigate('/');
      })
      .catch(error => alert(error.message))
  }
  const register = e => {
    e.preventDefault();
    //Firebase register
    auth
      .createUserWithEmailAndPassword(email, password)
      .then((auth) => {
        console.log(auth);
        if (auth) {
          navigate('/');
        }
      })
      .catch(error => alert(error.message))
  }
  return (
    <div className='login'>
      <Link to='/'>
        <img className='login_logo' src='https://cdn-icons-png.flaticon.com/512/195/195482.png' alt='' />
      </Link>
        <h1> SuitnTux </h1>
        <div className='login_container'>
          <h1> Sign In </h1>
          <form>
            <h5> Email </h5>
            <input type='text' value={email} onChange={e => setEmail(e.target.value)} />
            <h5> Password </h5>
            <input type='password' value={password} onChange={e => setPassword(e.target.value)} />
            <button type='submit' onClick={signIn} className='login_signInButton'> Sign In </button>
          </form>
          <p> By continuing, you agree to The Big Day's Terms of Use and Privacy Notice. </p>
          <button onClick={register} className='login_registerButton'> Create your The Big Day account </button>
        </div>
    </div>
  )
}

export default Login